<template>
  <div class="store-distace-list">
    <div
      v-for="(store, key) in storeListWithDistance"
      :key="`${store.store_id}__${key}`"
      class="store-distance"
    >
      <router-link class="store-distance__url" :to="`/${store.store_url}`">
        <div class="store-distance__name ellipsis">{{ getTranslatedName(store) }}</div>
        <div
          v-if="!isMissingAddressData && store.distance"
          class="store-distance__distance"
        >
          ({{ store.distance }}&nbsp;Km)
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import getCrowFliesDistance from "@/util/getCrowFliesDistance";
import getStringByLocale from "@/util/getStringByLocale";

export default {
  name: "GridSearchItemStoreDistance",
  props: {
    storeList: Array,
  },
  computed: {
    ...mapGetters(["searchAnchorPoint", "isMissingAddressData"]),
    storeListWithDistance() {
      const { calculateStoreDistance, storeList = [] } = this;
      return storeList
        .map(store => ({
          ...store,
          distance: calculateStoreDistance(store.geolocation),
        }))
        .sort((a, b) => a.distance - b.distance);
    },
  },
  methods: {
    calculateStoreDistance(coorStr) {
      const { searchAnchorPoint, getCoordinateFromString } = this;
      const coordinates = getCoordinateFromString(coorStr);
      if (!searchAnchorPoint) return;
      // 1.22 to 1.3, 2.00 to 2
      return (
        Math.ceil(getCrowFliesDistance(searchAnchorPoint, coordinates) * 10) /
        10
      );
    },
    getCoordinateFromString(coorStr) {
      const [lat, lng] = coorStr.replace(/\s/g, "").split(",");
      return { lat, lng };
    },
    getTranslatedName(store) {
      return getStringByLocale({
        isStr: store.store_name,
        enStr: store.en_store_name,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.store-distance__url {
  display: flex;
  /* justify-content: space-between; */
  color: #8d8d8f;
  &:hover {
    color: #616161;
  }
}
.store-distance__name {
  padding-right: 10px;
}
</style>
