<template>
  <div class="grid-search-item">
    <div class="items-grid__item">
      <div class="items-grid__image">
        <router-link :to="urlStripped" @click.native="redirectToUrl">
          <ImageViewer :alt="name" :src="cloudinaryImage" loading="lazy" />
          <div class="item-grid__image-overlay">
            <div class="center-center width">
              <base-button class="primary">{{ $t("view") }}</base-button>
            </div>
          </div>
        </router-link>
      </div>
      <div class="items-grid__content">
        <div class="items-grid__left clearfix">
          <h2 class="items-grid__name">
            <router-link :to="urlStripped" @click.native="redirectToUrl">{{
              translatedName
            }}</router-link>
          </h2>
          <div class="items-grid__store-distances">
            <GridSearchItemStoreDistance :storeList="stores" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<i18n>
{
  "is": {
    "view": "Skoða"
  },
  "en": {
    "view": "View"
  }
}
</i18n>

<script type="text/javascript">
import BaseButton from "@/components/BaseButton";
import ImageViewer from "@/components/ImageViewer";
import GridSearchItemStoreDistance from "@/components/GridSearchItemStoreDistance";
import { mapActions } from "vuex";
import getStringByLocale from "@/util/getStringByLocale";
import CloudinaryQuery from "@/util/CloudinaryQuery";

export default {
  name: "GridSearchItem",
  components: {
    BaseButton,
    GridSearchItemStoreDistance,
    ImageViewer,
  },
  props: {
    price: Number,
    name: String,
    en_name: String,
    image: String,
    special_price: String,
    objectID: String,
    attribute_set_name: String,
    stores: Array,
    url: String,
    _geoloc: [Array, Object],
  },
  computed: {
    imgRelativeURL() {
      return this.image && this.image.includes("http")
        ? this.image.replace("https://cdn.aha.is/media/catalog/product/", "")
        : this.image;
    },
    cloudinaryImage() {
      if (
        !this.image ||
        this.image === "no_selection" ||
        this.image.indexOf("no_selection") !== -1
      ) {
        return "";
      }

      return new CloudinaryQuery(this.imgRelativeURL).height(150).toURL();
    },
    parentUrl() {
      const url = this.stores ? this.stores[0].store_url : "/404";
      return url;
    },
    urlStripped() {
      const { attribute_set_name: type, url: urlFull, parentUrl } = this;
      const url = urlFull.replace("https://www.aha.is/", "");
      switch (type) {
        case "Store":
          return `/verslun/${url}`;
        case "Store Item":
          return `/${url}`;
        case "Restaurants":
          return `/veitingar/${url}`;
        case "Food":
          return `/veitingar/${parentUrl}`;
        case "Aha":
          return `/tilbod-dagsins/${url}`;
        default:
          return "/404";
      }
    },
    translatedName() {
      return getStringByLocale({ isStr: this.name, enStr: this.en_name });
    },
  },
  methods: {
    ...mapActions(["addToCart", "stopSearching"]),
    addToCart() {
      this.addToCart({
        product: this.entity_id,
        super_product: this.parent_product_id,
      });
    },
    redirectToUrl() {
      const { url: urlFull, stopSearching } = this;
      const url = urlFull.replace("https://www.aha.is/", "");
      this.$route.path === `/${url}` && stopSearching();
    },
  },
};
</script>

